.flex-container {
  display: flex;
  justify-content: center;
  background-color: DodgerBlue;
  height: 230px;
}
  
  
  .flex-container > div {
    background-color: rgba(234, 203, 203, 0.985);
    width: 200px;
    margin: 10px;
    text-align: center;
    line-height: 75px;
    font-size: 30px;
    border-radius: 10%;
    
  }

  .Week {
    background-color:rgb(162, 153, 153);
   
    margin: 10px;
    text-align: center;
    line-height: 75px;
    font-size: 40px;
  }
   
  .switch{
    border-radius: 20%;
    background-color: rgb(204, 195, 195);
  }
  


  /* ------------------------------------------------------------------------------------------------------------------ */

  .switch3-container h5{
    margin-bottom: .7rem;
  }
  .switch3
  {
  
    position: relative;
    height:50px;
    display:flex;
    justify-content:space-around;
  }
  .switch3 label{
    padding: 1rem;
    z-index:1;
  }
  .switch3::before{
    content: '';
    position: absolute;
    left:0;
    width: 34%;
    height: 100%;
    background: #14142a;
    border: 1px solid #fff;
    border-radius: 12px;
    opacity: .5;
    transition: .5s left ease;
  }
  
  .switch3:has(#switch3-radio1:checked)::before{
    left: 0%;
  }
  .switch3:has(#switch3-radio2:checked)::before{
    left: 33%;
  }
  .switch3:has(#switch3-radio3:checked)::before{
    left: 66%;
  }